import React from "react";
import logo from "../../assets/images/2gather.jpg";
import { isValidEmail } from "../../platform/validatior";
import eyeIcon from "../../assets/images/close-eye.png";
import InputField from "./InputField";

import "./style.scss";

class SignIn extends React.Component {
  state = {
    form: {
      email: "",
      password: ""
    },
    focus: {
      email: false,
      password: false
    },
    passToggle: false,
    submitted: false
  };

  submit = async e => {
    e.preventDefault();
    const {
      form: { email, password }
    } = this.state;

    this.setState({
      submitted: true
    });

    if (isValidEmail(email) && password) {
      try {
        const response = await fetch("https://2.evollai.com/api/auth/login", {
          method: "POST",
          body: JSON.stringify({
            email,
            password,
            osType: 3
          }),
          headers: {
            "Content-Type": "application/json",
            "Accept-Language": "*",
            language: 1
          }
        });
        const result = await response.json();

        if (result) {
          localStorage.setItem("authToken", result.data.authToken);
          window.routerHistory.push("/topic-suggestion");
        }
      } catch (error) {
        console.log(error);
      }
    }
  };

  change = ({ target: { name, value } }) => {
    const { form } = this.state;
    form[name] = value;
    this.setState({ form });
  };

  togglePassword = () => {
    this.setState({
      passToggle: !this.state.passToggle
    });
  };

  render() {
    const { form, focus, passToggle, submitted } = this.state;
    return (
      <div className="sign-in">
        <div className="sign-in-form">
          <div className="sign-in-form-container">
            <h2>Sign in</h2>
            <p>
              Enter your email and password and login in your <br /> account
            </p>
            <form>
              <div className="fields">
                <InputField
                  change={this.change}
                  labelName="Email"
                  name="email"
                  type="email"
                />
                <div className="password-label">
                  <InputField
                    change={this.change}
                    labelName="Password"
                    name="password"
                    type={passToggle ? "text" : "password"}
                  />
                  <img
                    src={eyeIcon}
                    alt="closed"
                    onClick={this.togglePassword}
                  />
                </div>
              </div>

              <button className="G-button" onClick={this.submit}>
                Sign in
              </button>
            </form>
          </div>
        </div>
        <div className="logo">
          <img src={logo} alt="2gather" />
        </div>
      </div>
    );
  }
}

export default SignIn;
